@font-face {
    font-family: "Far East";
    src: url('./fonts/fareast.ttf') format('embedded-opentype'), /* Internet Explorer */
    url('./fonts/fareast.ttf') format('woff2'), /* Super Modern Browsers */
    url('./fonts/fareast.ttf') format('woff'), /* Pretty Modern Browsers */
    url('./fonts/fareast.ttf') format('truetype'), /* Safari, Android, iOS */
    url('./fonts/fareast.ttf') format('svg');
    /* Legacy iOS */
}

p {
    margin: 0;
}

.japan-spirit {
    font-family: "Far East";
    text-indent: -9rem;
    padding-left: 3rem;
}

body {
    padding: 0;
    margin: 0;
    color: white;
    height: 100vh;
}

.center-all {
    display: flex;
    align-items: center;
    justify-content: center;
}

article.card {
    background-color: rgba(25, 25, 25, 0.75) !important;
    margin-left: 5px;
    margin-right: 5px;
}

#root, #box {
    height: 100%;
}

main {
    background-image: url("https://source.unsplash.com/collection/1252289");
    background-size: 100vw 100vh;
    background-repeat: no-repeat;
}

#overlay {
    background-color: black;
    /* height: 100%; */
    /* blank space at the top and bottom of div on real mobile device */
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 100;
    opacity: 1;
}

.rising-sun {
    position: absolute;
    margin-right: 3rem;
}

.japan-spirit > p {
    z-index: 1;
    text-shadow: 0 0 5px black, 0 0 5px black, 0 0 5px black, 0 0 5px black;
}

@keyframes fadein {
    0% {
        opacity: 0;
        z-index: 100
    }
    100% {
        opacity: 1;
        z-index: 100
    }
}

@keyframes fadeout {
    0% {
        opacity: 1;
        z-index: 100
    }
    100% {
        opacity: 0;
        z-index: 0
    }
}

.fadein {
    animation-name: fadein;
    animation-duration: 2s;
    animation-fill-mode: forwards;
}

.fadeout {
    animation-name: fadeout;
    animation-duration: 2s;
    animation-fill-mode: forwards;
}